//*-----------------------------------------------
//|   Color System
//-----------------------------------------------*/

$white: #fff !default;
$gray-100: #fafafa !default;
$gray-200: #f2f2f2 !default;
$gray-300: #e1e1e1 !default;
$gray-400: #bebebe !default;
$gray-500: #949494 !default;
$gray-600: #7f7f7f !default;
$gray-700: #6c757d !default;
$gray-800: #555555 !default;
$gray-900: #404040 !default;
$gray-1000: #2b2b2b !default;
$gray-1100: #1f1f1f !default;
$black: #000 !default;

$grays: () !default;
$grays: map-merge(
	(
		'black': $black,
		'100': $gray-100,
		'200': $gray-200,
		'300': $gray-300,
		'400': $gray-400,
		'500': $gray-500,
		'600': $gray-600,
		'700': $gray-700,
		'800': $gray-800,
		'900': $gray-900,
		'1000': $gray-1000,
		'1100': $gray-1100,
		'white': $white
	),
	$grays
);
// $body-bg: $gray-200 !default;

//*-----------------------------------------------
//|   Solid Colors
//-----------------------------------------------*/
$blue: #057bde !default;
$indigo: #727cf5 !default;
$purple: #6b5eae !default;
$pink: #ff679b !default;
$red: #e7434e !default;
$orange: #ff9500 !default;
$yellow: #fdd428 !default;
$green: #4cd964 !default;
$teal: #02a8b5 !default;
$cyan: #2eb7f5 !default;

//*-----------------------------------------------
//|   Theme Colors
//-----------------------------------------------*/
$primary: $blue !default;
$secondary: $yellow !default;
$success: $green !default;
$info: $cyan !default;
$warning: $orange !default;
$danger: $pink !default;
$light: $gray-100 !default;
$dark: $gray-1100 !default;

$theme-colors: () !default;
$theme-colors: map-merge(
	(
		'primary': $primary,
		'secondary': $secondary,
		'success': $success,
		'info': $info,
		'warning': $warning,
		'danger': $danger,
		'light': $light,
		'dark': $dark,
		'white': $white
	),
	$theme-colors
);

$dark-grays: (
	'black': $white,
	'100': $gray-1100,
	'200': $gray-1000,
	'300': $gray-900,
	'400': $gray-800,
	'500': $gray-700,
	'600': $gray-600,
	'700': $gray-500,
	'800': $gray-400,
	'900': $gray-300,
	'1000': $gray-200,
	'1100': $gray-100,
	'white': $black
);
