//*-----------------------------------------------
//|   Cards
//-----------------------------------------------*/
$card-spacer-y: map_get($spacers, 4) !default;
$card-spacer-x: map_get($spacers, 4) !default;
$card-border-width: 0 !default;
$card-border-radius: $border-radius-lg !default;
$card-border-color: var(--#{$variable-prefix}card-border-color) !default;
$card-cap-bg: var(--#{$variable-prefix}card-cap-bg) !default;
$card-cap-padding-y: $spacer !default;
$card-cap-padding-x: 1.25rem !default;
$card-bg: var(--#{$variable-prefix}card-bg);

//*=============================================
//|   Theme Specific
//=============================================

//*-----------------------------------------------
//|   Viewport Heights & Widths
//----------------------------------------------

$viewport-heights: () !default;
$viewport-heights: map-merge(
  (
    25: 25vh,
    50: 50vh,
    75: 75vh,
    100: 100vh,
  ),
  $viewport-heights
);

$viewport-widths: () !default;
$viewport-widths: map-merge(
  (
    25: 25vw,
    50: 50vw,
    75: 75vw,
    100: 100vw,
  ),
  $viewport-widths
);

$sizes: () !default;
$sizes: map-merge(
  (
    25: 25%,
    50: 50%,
    75: 75%,
    100: 100%,
    auto: auto,
  ),
  $sizes
);

$gutters: () !default;
$gutters: map-merge(
  $spacers,
  (
    card: $card-spacer-x * 2,
  )
);

//*-----------------------------------------------
//|   Scrollbar
//-----------------------------------------------*/
$scrollbar-bg: var(--#{$variable-prefix}scrollbar-bg) !default;
